"use client"

import { useState, useEffect, useRef } from "react"
import { Send, Sparkles, Plus } from "lucide-react"

interface Message {
  id: string
  role: "user" | "assistant"
  content: string
  timestamp: string
}

export default function AIPage() {
  const [messages, setMessages] = useState<Message[]>([])
  const [input, setInput] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const stored = localStorage.getItem("aiMessages")
    if (stored) {
      setMessages(JSON.parse(stored))
    }
  }, [])

  useEffect(() => {
    localStorage.setItem("aiMessages", JSON.stringify(messages))
  }, [messages])

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages])

  const aiResponses = [
    {
      keyword: ["schedule", "block"],
      response:
        "Great! I recommend breaking your tasks into 90-minute focused work blocks with 15-minute breaks. Would you like me to help you plan your week?",
    },
    {
      keyword: ["productivity", "improve"],
      response:
        "Here are some proven productivity tips:\n1. Time block your calendar\n2. Work in 90-minute sprints\n3. Take regular breaks\n4. Batch similar tasks\n5. Eliminate distractions",
    },
    {
      keyword: ["task", "todo"],
      response:
        "I can help you manage your tasks better! Try organizing them by priority and time blocks. Use the Weekly To-Do section to plan your week.",
    },
    {
      keyword: ["help"],
      response:
        "I can assist you with:\n- Productivity tips\n- Time management strategies\n- Task organization\n- Schedule planning\n- Motivation and focus techniques",
    },
  ]

  const getAIResponse = (userMessage: string) => {
    const lowerMessage = userMessage.toLowerCase()
    for (const { keyword, response } of aiResponses) {
      if (keyword.some((k) => lowerMessage.includes(k))) {
        return response
      }
    }
    return "That sounds interesting! Keep up with your time blocking and stay focused. Remember to take breaks to maintain productivity."
  }

  const handleSendMessage = async () => {
    if (!input.trim()) return

    // Add user message
    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content: input,
      timestamp: new Date().toISOString(),
    }

    setMessages((prev) => [...prev, userMessage])
    setInput("")
    setIsLoading(true)

    // Simulate AI response delay
    setTimeout(() => {
      const aiResponse: Message = {
        id: (Date.now() + 1).toString(),
        role: "assistant",
        content: getAIResponse(input),
        timestamp: new Date().toISOString(),
      }
      setMessages((prev) => [...prev, aiResponse])
      setIsLoading(false)
    }, 800)
  }

  const handleNewChat = () => {
    if (window.confirm("Start a new conversation?")) {
      setMessages([])
    }
  }

  return (
    <div className="p-4 lg:p-8 flex flex-col h-screen overflow-hidden">
      {/* Header */}
      <div className="mb-6 flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-foreground flex items-center gap-2">
            <Sparkles className="text-secondary" />
            AI Assistant
          </h1>
          <p className="text-muted-foreground">Get personalized productivity advice</p>
        </div>
        <button
          onClick={handleNewChat}
          className="flex items-center gap-2 bg-secondary hover:bg-secondary/90 text-secondary-foreground px-4 py-2 rounded-lg font-medium transition-colors"
        >
          <Plus size={20} />
          New Chat
        </button>
      </div>

      {/* Messages Container */}
      <div className="flex-1 overflow-y-auto mb-4 space-y-4 bg-background rounded-lg p-4">
        {messages.length === 0 ? (
          <div className="flex flex-col items-center justify-center h-full text-center">
            <Sparkles className="w-16 h-16 text-muted-foreground/30 mb-4" />
            <h2 className="text-xl font-bold text-foreground mb-2">Welcome to AI Assistant</h2>
            <p className="text-muted-foreground max-w-md">
              Ask me anything about productivity, time management, or getting the most out of your calendar!
            </p>
          </div>
        ) : (
          messages.map((message) => (
            <div key={message.id} className={`flex ${message.role === "user" ? "justify-end" : "justify-start"}`}>
              <div
                className={`max-w-xs lg:max-w-md px-4 py-3 rounded-lg ${
                  message.role === "user"
                    ? "bg-primary text-primary-foreground rounded-br-none"
                    : "bg-secondary text-secondary-foreground rounded-bl-none"
                }`}
              >
                <p className="whitespace-pre-wrap text-sm">{message.content}</p>
                <p
                  className={`text-xs mt-1 ${
                    message.role === "user" ? "text-primary-foreground/70" : "text-secondary-foreground/70"
                  }`}
                >
                  {new Date(message.timestamp).toLocaleTimeString([], {
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </p>
              </div>
            </div>
          ))
        )}
        {isLoading && (
          <div className="flex justify-start">
            <div className="bg-secondary text-secondary-foreground px-4 py-3 rounded-lg rounded-bl-none">
              <div className="flex gap-2">
                <div className="w-2 h-2 bg-secondary-foreground rounded-full animate-bounce"></div>
                <div
                  className="w-2 h-2 bg-secondary-foreground rounded-full animate-bounce"
                  style={{ animationDelay: "0.2s" }}
                ></div>
                <div
                  className="w-2 h-2 bg-secondary-foreground rounded-full animate-bounce"
                  style={{ animationDelay: "0.4s" }}
                ></div>
              </div>
            </div>
          </div>
        )}
        <div ref={messagesEndRef} />
      </div>

      {/* Input Area */}
      <div className="flex gap-2">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyPress={(e) => e.key === "Enter" && handleSendMessage()}
          placeholder="Ask about productivity, time management, schedules..."
          className="flex-1 px-4 py-3 border border-border rounded-lg bg-card text-foreground placeholder-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary"
        />
        <button
          onClick={handleSendMessage}
          disabled={isLoading || !input.trim()}
          className="bg-secondary hover:bg-secondary/90 disabled:opacity-50 disabled:cursor-not-allowed text-secondary-foreground px-4 py-3 rounded-lg font-medium transition-colors flex items-center gap-2"
        >
          <Send size={20} />
        </button>
      </div>
    </div>
  )
}
